import React from "react"
import './ItemDetail.css'


const ItemDetail = ({data}) => {
    
    return (
        <div className="card-api">
        <h1> {data.title} </h1>
        <img src={data.hdurl} alt='estrellas'/>
        <p>{data.explanation}</p>
        
        </div>
    )
}
export default ItemDetail